body {
  --color-primary: #88c9f5;
  --color-secondary: #2980b9;
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.highlight {
  background-color: var(--color-primary);
  padding-top: 1rem;
}

.highlight h1 {
  margin-bottom: 1.5rem;
}

.highlight .banner {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

h1 {
  background-color: var(--color-primary);
  color: #fff;
  text-align: center;
  text-shadow: 1px 1px 2px #000;
  border: 2px solid #fff;
  margin-bottom: 20px;
  padding: 20px;
  font-size: 3rem;
  font-weight: bold;
}

.banner {
  max-width: 1200px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin: 0 auto;
  display: grid;
}

.banner img {
  max-width: 100%;
  height: auto;
  border-bottom: 10px solid var(--color-secondary);
  margin: 0 auto;
}

.banner a {
  display: inline-block;
  position: relative;
}

.banner a:hover {
  color: #fff;
  text-shadow: -1px -1px #000, 1px -1px #000, -1px 1px #000, 1px 1px #000;
  font-size: 1.5rem;
  transform: scale(1.1);
}

.banner a:hover:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: #00000080;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.banner a:hover span {
  color: #fff;
  z-index: 2;
  font-size: 1.25rem;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.banner a span {
  display: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

#grid {
  width: 100%;
  margin: 70px 0;
}

#grid td {
  width: 30%;
  height: 0;
  text-align: center;
  vertical-align: middle;
  background-color: #fff;
  border-radius: 20px;
  padding-bottom: 30%;
  position: relative;
}

#grid td.gastronomia {
  background-blend-mode: multiply;
  opacity: .8;
  background-image: url("gastronomia.28a13bce.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#grid td.musica {
  background-blend-mode: multiply;
  opacity: .8;
  background-image: url("musica.cfaf7686.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#grid td.arte {
  background-blend-mode: multiply;
  opacity: .8;
  background-image: url("arte.0e936844.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#grid td.cultura {
  background-blend-mode: multiply;
  opacity: .8;
  background-image: url("cultura.ce110eb0.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#grid td.templos {
  background-blend-mode: multiply;
  opacity: .8;
  background-image: url("templos.b6d6aaeb.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#grid td.historia {
  background-blend-mode: multiply;
  opacity: .8;
  background-image: url("historia.6250943e.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#grid td a {
  width: 100%;
  height: 100%;
  color: #fff;
  text-shadow: -1px -1px #000, 1px -1px #000, -1px 1px #000, 1px 1px #000;
  opacity: .7;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  text-decoration: none;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

#grid td:hover {
  background-color: var(--color-secondary);
}

#grid td:hover a {
  color: #fff;
}

@media screen and (max-width: 768px) {
  #grid tr {
    margin-bottom: 1rem;
    display: block;
  }

  #grid td {
    width: 100%;
    margin-right: 2%;
    display: inline-block;
  }

  #grid td:nth-child(2n) {
    margin-right: 0;
  }

  .banner {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media (max-width: 480px) {
  #grid td {
    width: 100%;
  }
}

